@import "../../css/shared.scss";

.p-home{
  padding: px(210) px(52) px(55) px(52);
  text-align: center;

  .coin-icon{
    width: px(150);
    height: auto;
  }

  .balance{
    margin: px(64) 0 0 0;
    font-size: px(45);
    line-height: px(50);

    span{
      font-size: px(66);
      margin-left: px(20);
      vertical-align: bottom;
      color: #ffd75e;
      line-height: px(50);
    }
  }

  .btns{
    padding: px(90) 0 px(145) 0;
    border-bottom: 1px solid #808080;
    display: flex;
    justify-content: space-evenly;
  }

  .price{
    font-size: px(50);
    color: #9b59ff;

    span{
      font-size: px(66);
      margin-left: px(20);
      vertical-align: bottom;
      color: #ffd75e;
      text-decoration: underline;

      i{
        font-style: normal;
        font-size: px(45);
      }
    }
  }

  .notice{
    font-size: px(30);
    color: #b2b2b2;
  }

  .input{
    margin-top: px(55);
    margin-bottom: px(162);

    .input-wrapper{
      display: inline-block;
      position: relative;

      i{
        display: block;
        position: absolute;
        width: 100%;
        height: 100%;
        background: transparent;
        top: 0;
      }
    }

    input {
      width: px(572);
      height: px(92);
      background-color: rgba(90, 47, 159, .2);
      border: 1px solid #cfc0c6;
      border-radius: px(16);
      font-size: px(30);
      color: #b2b2b2;
      padding: 0 px(18);
      box-sizing: border-box;
    }

    img{
      height: px(58);
      margin-left: px(20);
      vertical-align: middle;
      font-size: px(30);
    }

  }

  .invite{
    margin-top: px(55);
    margin-bottom: px(162);
    text-align: left;
    padding: px(30);
  }

  .package-json{
    text-align: center;
    color: #ecc65a;
    font-size: 12px;
    margin-top: px(120);
  }
}
